import { Box as MUIBox, Popper } from '@mui/material';
import React, { useState } from 'react';
import { Grid } from '../../../../../components/common/Grid/Grid';
import BoxMini from '../../../../../components/layout/BoxMini';
import ModalContainerSub from '../../../../../components/layout/ModalContainerSub';
import EditBtnSM from '../../../../../components/UI/Atoms/Button/EditBtnSM';
import InfoData from '../../../../../components/UI/Atoms/Info/InfoData';
import InfoLabelMD from '../../../../../components/UI/Atoms/Info/InfoLabelMD';
import Switch from '../../../../../components/UI/Atoms/Switch/Switch';
import ProfileImage from '../../../../../components/UI/Atoms/Profile/ProfileImage';
import { useDebug } from '../../../../../hooks/basic/useDebug';
import { useModal } from '../../../../../hooks/basic/useModal';
import { useInputs } from '../../../../../hooks/input/useInputs';
import { toGender } from '../../../../../utils/render/toGender';
import DatepickerSM from '../../../../../components/UI/Atoms/Datepicker/DatepickerSM';
import SelectInput from '../../../../../components/common/Select/SelectInput';
import BasicTextArea from '../../../../../components/UI/Atoms/Textarea/BasicTextArea';
import WhiteBtnSM from '../../../../../components/UI/Atoms/Button/WhiteBtnMD';
import BlackBtnSM from '../../../../../components/UI/Atoms/Button/BlackBtnSM';
import { useMutation } from '@tanstack/react-query';
import { createBill } from '../../../../../api/Bill/billApi';
import { toAge } from '../../../../../utils/render/toAge';
import IconButton from '@mui/material/IconButton';
import CommentOutlinedIcon from '@mui/icons-material/CommentOutlined';
import RankImage from '../../../../../components/common/Rank/RankImage';
import Em from '../../../../../components/UI/Atoms/Ex/Em'
import { isJSDocReturnTag } from 'typescript'
import { numberWithCommas } from '../../../../../utils/lib/Regex'

const BillList = ({ data, year, month, refetch, totalRefetch }) => {
  const [anchorEl, setAnchorEl] = useState('');
  const init = Array.from({ length: data?.length }, (v) => false);
  const [openPop, setOpenPop] = useState(init);

  //custom hook
  const [open, openFn, closeFn] = useModal();
  const [editData, setEditData, handleChange] = useInputs({});

  // query
  const { mutate: submitMutate, isLoading: submitLoading } = useMutation(([studentId, body]) =>
    createBill([studentId, body]),
  );

  // handler
  const handlePopper = (e) => {
    let t = e.currentTarget;
    let temp = [...init];

    if (openPop[t.id]) {
      setAnchorEl(null);
    } else {
      setAnchorEl(t);
      temp[t.id] = true;
    }
    setOpenPop([...temp]);
  };

  const handleEdit = (e) => {
    let idx = e.currentTarget.id;
    setEditData({ ...data[idx] });
    setTimeout(() => openFn(), 200);
  };

  const handleSwitch = (e) => {
    let chk = e.target.checked;
    if (chk) {
      setEditData({ ...editData, is_paid: 1 });
    } else {
      setEditData({ ...editData, is_paid: 0 });
    }
  };

  const handleSubmit = (e) => {
    if (editData?.is_paid === 1) {
      if (editData?.paid_date === '0000-00-00') {
        alert('납부일을 입력해주세요.');
        return;
      }
      if (editData?.payment_method === '') {
        alert('납부방법을 선택해주세요.');
        return;
      }
    }

    const studentId = editData['StudentInfo.id'] || '';
    const isPaid = editData?.is_paid;
    if (isPaid === 0) {
      let body = {
        is_paid: 0,
        paid_year: year,
        paid_month: month,
        paying_amount: editData?.paying_amount,
        payment_method: '',
        payment_note: '',
        paid_date: '',
      };
      submitMutate([studentId, body], {
        onSuccess: () => {
          totalRefetch();
          refetch().then(() => closeFn());
        },
      });
    } else {
      let body = {
        is_paid: 1,
        paid_year: year,
        paid_month: month,
        paying_amount: editData?.paying_amount,
        payment_method: editData?.payment_method,
        payment_note: editData?.payment_note,
        paid_date: editData?.paid_date,
      }
      if(body.paid_date == null){
        alert('납부일을 선택해주세요.')
        return
      }
      if(body.payment_method == ''){
        alert('납부 방법을 선택해주세요.')
        return
      } 
      else {
        submitMutate([studentId, body], {
          onSuccess: () => {
            totalRefetch();
            refetch().then(()=>closeFn());
          } 
        })
      }
    }
  };

  // useDebug(data)
  return (
    <>
      <Grid full>
        <Grid.Head col className="text-[16px]">
          <Grid.Col fixed="175px">　</Grid.Col>
          <Grid.Col fixed="170px">　</Grid.Col>
          <Grid.Col fixed="160px">　</Grid.Col>
          <Grid.Col fixed="105px">청구일</Grid.Col>
          <Grid.Col fixed="140px">수련비</Grid.Col>
          <Grid.Col fixed="120px">납부상태</Grid.Col>
          <Grid.Col fixed="120px">납부일</Grid.Col>
          <Grid.Col fixed="100px">납부방법</Grid.Col>
          <Grid.Col fixed="110px" className="pl-[25px]">
            메모
          </Grid.Col>
        </Grid.Head>
        <Grid.RowWrap className="text-[15px]">
          {data &&
            React.Children.toArray(
              data?.map((item, idx) => (
                <Grid.Row>
                  <Grid.Col fixed="180px">
                    <div className="flex gap-2 items-center">
                      <ProfileImage urlKey={item['StudentInfo.photo_url']} />
                      {item['StudentInfo.last_name']}
                      {item['StudentInfo.first_name']}
                    </div>
                  </Grid.Col>
                  <Grid.Col fixed="170px" className="pr-0">
                    <div className="flex">
                      <span className="w-[60px]">
                        <RankImage src={item['StudentInfo.level']} className='pt-[3px]' />
                      </span>
                      <span className="w-[60px]">{toGender(item['StudentInfo.sex'])}</span>
                      <span className="w-[60px]">{toAge(item['StudentInfo.dob'])}</span>
                    </div>
                  </Grid.Col>
                  <Grid.Col fixed="160px">{item?.class_info?.title}</Grid.Col>
                  <Grid.Col fixed="100px">
                    {item['StudentInfo.due_date'] ? `${item['StudentInfo.due_date']}일` : ''}
                  </Grid.Col>
                  <Grid.Col fixed="140px">{numberWithCommas(item['paying_amount'])}</Grid.Col>
                  <Grid.Col fixed="120px">{item['is_paid'] === 0 ? '미납' : '납부'}</Grid.Col>
                  <Grid.Col fixed="120px" className="tracking-tight">
                    {item?.paid_date}
                  </Grid.Col>
                  <Grid.Col fixed="100px">{item['payment_method']}</Grid.Col>
                  <Grid.Col fixed="110px" className="flex items-center">
                    <IconButton id={idx} onClick={handlePopper}>
                      <CommentOutlinedIcon color="action" />
                    </IconButton>
                  </Grid.Col>
                  <Popper id={idx} open={openPop[idx]} anchorEl={anchorEl}>
                    <MUIBox
                      sx={{
                        border: 1,
                        borderRadius: '5px',
                        p: 2,
                        bgcolor: '#fafafa',
                        width: '220px',
                        height: '120px',
                        mt: '5px',
                        ml: 22,
                      }}
                    >
                      {item['payment_note']}
                    </MUIBox>
                  </Popper>
                  <EditBtnSM id={idx} onClick={handleEdit}>
                    수정
                  </EditBtnSM>
                </Grid.Row>
              )),
            )}
        </Grid.RowWrap>
      </Grid>

      <ModalContainerSub open={open} onClose={closeFn}>
        {editData && (
          <>
            <BoxMini>
              <div className="flex flex-col items-center gap-[10px]">
                <div className="flex gap-[10px] items-center">
                  <ProfileImage urlKey={editData['StudentInfo.photo_url']} />
                  {editData['StudentInfo.last_name']}
                  {editData['StudentInfo.first_name']}
                </div>
                <div className="flex gap-[10px]">
                  <div>{editData['StudentInfo.level']}</div>
                  <div>{toGender(editData['StudentInfo.sex'])}</div>
                  <div>{toAge(editData['StudentInfo.dob'])}</div>
                </div>
              </div>
            </BoxMini>
            <BoxMini>
              <div className="">
                <div className="flex items-center mb-[5px]">
                  <InfoLabelMD className="text-[16px] flex-none w-[107px]">청구일</InfoLabelMD>
                  <InfoData>
                    {editData['StudentInfo.due_date'] &&
                      `매달 ${editData['StudentInfo.due_date']}일`}
                  </InfoData>
                </div>
                <div className="flex items-center">
                  <InfoLabelMD className="text-[16px] flex-none w-[107px]">수련비</InfoLabelMD>
                  <InfoData>{editData['paying_amount']}</InfoData>
                </div>
              </div>
            </BoxMini>
            <BoxMini>
              <div className="flex flex-col gap-[10px]">
                <div className="flex items-center">
                  <InfoLabelMD className="text-[16px]">납부상태</InfoLabelMD>
                  <Switch
                    name="is_paid"
                    label={editData?.is_paid === 0 ? '미납' : '납부'}
                    checked={editData?.is_paid === 0 ? false : true}
                    onChange={handleSwitch}
                  />
                </div>
                {editData?.is_paid === 1 && (
                  <>
                    <div className="flex items-center mt-[5px]">
                      <InfoLabelMD className="text-[16px]">납부일*</InfoLabelMD>
                      <DatepickerSM
                        length={186}
                        name="paid_date"
                        value={editData?.paid_date || ''}
                        onChange={handleChange}
                        className="p-[14px]"
                      ></DatepickerSM>
                    </div>
                    <div className="flex items-center mt-[5px]">
                      <InfoLabelMD className="text-[16px]">납부방법*</InfoLabelMD>
                      <SelectInput
                        name="payment_method"
                        value={editData?.payment_method || ''}
                        onChange={handleChange}
                        payment
                        className="text-[14px]"
                      ></SelectInput>
                    </div>
                    <div className="flex items-start mt-[5px]">
                      <InfoLabelMD className="mt-[3px] basis-1/4 text-[16px]">납부메모</InfoLabelMD>
                      <BasicTextArea
                        className="ml-[-12px]"
                        length={275}
                        placeholder="이번달 납부의 특이사항 등을 적어주세요."
                        name="payment_note"
                        value={editData?.payment_note || ''}
                        onChange={handleChange}
                      />
                    </div>
                  </>
                )}
              </div>
            </BoxMini>
            <div className="flex justify-center mt-5 gap-1">
              <WhiteBtnSM onClick={closeFn}>취소</WhiteBtnSM>
              <BlackBtnSM onClick={handleSubmit} disabled={submitLoading}>
                등록
              </BlackBtnSM>
            </div>
          </>
        )}
      </ModalContainerSub>
    </>
  );
};

export default BillList;