export const getBillMonth = async (dojangId, classId, year, month) => {
  const res = await authApi.get(`student/studentbillread/${dojangId}/${classId}/${year}/${month}`)
  return res.data
}

export const createBill = async ([studentId, body]) => {
  const res = await authApi.post(`student/studentbillcreate/${studentId}`, body)
  return res
}

export const getTotalBill = async (dojangId, classId, year, month) => {
  const res = await authApi.get(`student/studentbilltotalread/${dojangId}/${classId}/${year}/${month}`)
  return res.data
}