import React from 'react'
import { Box as MUIBox } from '@mui/material';

const style = {
  bgcolor: '#ffffff',
  borderRadius: '5px',
  mt: 1,
  mb: 1,
  pt: '20px',
  px: '20px',
  pb: '20px',
};

const BoxMini = ({ children, ...props }) => {
  return (
    <>
    <MUIBox sx={style} {...props}>
      {children}
    </MUIBox>
    </>
  )
}

export default BoxMini