import React from 'react'
import MenuItem from '@mui/material/MenuItem';
import { Select } from '@mui/material';

const selectItemStyle = {
  height: '30px',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    fontWeight: 'bold',
    backgroundColor: '#f3f3f3',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
}

const SelectInput = ({payment, value, onChange, ...props}) => {

  const paymentList = [
    { id: '현금', title: '현금' },
    { id: '카드', title: '카드' },
    { id: '이체', title: '이체' },
  ]


  return (
    <>
      <Select 
        sx={{width: '186px', height: '30px', minWidth:"186px"}}
        size='small'
        value={value}
        onChange={onChange}
        {...props}
      >
        { payment &&
          React.Children.toArray(
            paymentList?.map(({id, title}) =>
              <MenuItem value={id} sx={selectItemStyle}
              >
                {title}
              </MenuItem>
            )
          )
        }
      </Select>
    </>
  )
}

export default SelectInput