import { Typography } from '@mui/material';
import { useQuery } from '@tanstack/react-query';
import React, { useRef } from 'react';
import { useRecoilValue } from 'recoil';
import { getBillMonth, getTotalBill } from '../../../../api/Bill/billApi';
import ClassSelectMD from '../../../../components/common/Class/ClassSelectMD';
import MonthSelect from '../../../../components/common/Select/MonthSelect';
import YearSelect from '../../../../components/common/Select/YearSelect';
import PageContainer from '../../../../components/layout/PageContainer';
import { useDebug } from '../../../../hooks/basic/useDebug';
import { useSelect } from '../../../../hooks/basic/useSelect';
import dojangIdAtom from '../../../../recoil/atom/dojangIdAtom';
import { getCurrMonth, getCurrYear } from '../../../../utils/basic/getDate';
import BillList from './BillList/BillList';

const BillResistPage = () => {
  const dojangId = useRecoilValue(dojangIdAtom);
  let yy = getCurrYear().toString();
  let mm = getCurrMonth().toString();

  // custom hook
  const [classId, handleChangeClasses] = useSelect('0');
  const [year, handleYear] = useSelect(yy);
  const [month, handleMonth] = useSelect(mm);

  // query
  const { data, refetch } = useQuery(
    ['getBillMonth', dojangId, classId, year, month],
    () => getBillMonth(dojangId, classId, year, month),
    {
      enabled: !!dojangId && !!classId && !!year && !!month,
      onSuccess: (res) => {
        // console.log(res)
      },
    },
  );
  const { data: total, refetch: totalRefetch } = useQuery(
    ['getTotalBill', dojangId, classId, year, month],
    () => getTotalBill(dojangId, classId, year, month),
    {
      enabled: !!dojangId && !!classId && !!year && !!month,
      onSuccess: (res) => {
        // console.log(res)
      },
    },
  );

  // useDebug(total);

  return (
    <>
      <PageContainer width="lg">
        <div className="flex justify-between items-center mb-3 w-full">
          <div className="flex gap-[5px]">
            <ClassSelectMD value={classId} onChange={handleChangeClasses} />
            <YearSelect value={year} onChange={handleYear} />
            <MonthSelect value={month} onChange={handleMonth} />
          </div>
        </div>
        <div className="flex w-full mb-[10px] gap-[50px] border border-solid border-[1px] border-[#dddddd] px-[20px] py-[10px] bg-[#f9f9f9]">
          <div className="flex items-center gap-[15px]">
            <Typography variant="h5" className="font-normal">
              · 이번달 총 수련비 :
            </Typography>
            <Typography variant="h5" className="text-[#000aff]">
              {total.total_paying_amount.toLocaleString('ko-kr')}원
            </Typography>
          </div>
          <div className="flex items-center gap-[15px]">
            <Typography variant="h5" className="font-normal">
              · 납부된 수련비 :
            </Typography>
            <Typography variant="h5" className="text-[#000aff]">
              {total.total_paid_amount.toLocaleString('ko-kr')}원
            </Typography>
          </div>
          <div className="flex items-center gap-[15px]">
            <Typography variant="h5" className="font-normal">
              · 미납 수련비 :
            </Typography>
            <Typography variant="h5" className="text-[#ff0000]">
              {total.total_non_paid_amount.toLocaleString('ko-kr')}원
            </Typography>
          </div>
        </div>
        <BillList
          year={year}
          month={month}
          data={data}
          refetch={refetch}
          totalRefetch={totalRefetch}
        />
      </PageContainer>
    </>
  );
};

export default BillResistPage;
